import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'

import IndexMenu from './components/IndexMenu.vue'
import Index from './components/Index.vue'
import About from './components/About.vue'
import Features from './components/Features.vue'
import Pricing from './components/Pricing.vue'
import Help from './components/Help.vue'
import LoginMenu from './components/LoginMenu.vue'
import Login from './components/Login.vue'
import SignupMenu from './components/SignupMenu.vue'
import Signup from './components/Signup.vue'
import UserMenu from './components/UserMenu.vue'
import User from './components/User.vue'
import CreateProject from './components/CreateProject.vue'
import ProjectNav from './components/ProjectNav.vue'

Vue.use(Router)

// 
// const apiRootPath =
//   process.env.NODE_ENV !== 'production' ? 'http://localhost:3000' : '/api'
const apiRootPath =
  process.env.NODE_ENV !== 'production' ? 'http://localhost:3000' : ''

//         
const pageCheck = (to, from, next) => {
  axios
    .post(`${apiRootPath}/users/verify`, {
      headers: {
        Authorization: localStorage.getItem('userToken')
      }
    })
    .then(result => {
      if (result.data.message) {
        if (result.data.message === 'jwt expired') {
          alert('  .  .')
        }
        if (result.data.message === 'jwt malformed') {
          alert(' .  .')
        }
        localStorage.removeItem('userToken')
        next('/')
      }
      next()
    })
    .catch(err => {
      throw err
    })
}

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      //    
      path: '/',
      component: IndexMenu,
      beforeEnter: (to, from, next) => {
        if (localStorage.getItem('userToken')) return next('/p')
        next()
      },
      children: [
        {
          path: '',
          name: 'index',
          component: Index
        },
        {
          path: '/about',
          name: 'about',
          component: About
        },
        {
          path: '/features',
          name: 'features',
          component: Features
        },
        {
          path: '/pricing',
          name: 'pricing',
          component: Pricing
        },
        {
          path: '/help',
          name: 'help',
          component: Help
        }
      ]
    },
    {
      //  
      path: '/login',
      component: LoginMenu,
      beforeEnter: (to, from, next) => {
        if (localStorage.getItem('userToken')) return next('/p')
        next()
      },
      children: [
        {
          path: '',
          name: 'login',
          component: Login
        }
      ]
    },
    {
      //  
      path: '/signup',
      component: SignupMenu,
      beforeEnter: (to, from, next) => {
        if (localStorage.getItem('userToken')) return next('/p')
        next()
      },
      children: [
        {
          path: '',
          name: 'signup',
          component: Signup
        }
      ]
    },
    {
      // 
      // '/:id':  id  
      path: '/project/:id',
      name: 'project',
      component: ProjectNav,
      beforeEnter: pageCheck
    },
    {
      //   
      path: '/p',
      component: UserMenu,
      beforeEnter: pageCheck,
      children: [
        {
          path: '',
          name: 'user',
          component: User
        },
        {
          path: '/p/create-project',
          name: 'create-project',
          component: CreateProject
        }
      ]
    },
    {
      //       
      path: '/*',
      redirect: '/'
    }
  ]
})
