<template>
  <v-app>
    <v-container grid-list-xl grid-list-lg class="content-index">
      <div>
        <span class="display-1 font-weight-bold">  </span>
        <br />
        <span class="display-1 font-weight-light">   .</span>
      </div>
      <br />
      <v-layout>
        <v-flex xl6 lg9 sm12>
          <v-form>
            <div class="headline"> </div>
            <div class="title-1 mb-2">   .</div>
            <v-text-field
              v-validate="'required|min:4'"
              :error-messages="errors.collect('projectName')"
              data-vv-name="projectName"
              v-model="form.projectName"
              required
              single-line
              solo
              class="mb-2"
            ></v-text-field>
            <div class="headline"> </div>
            <div class="title-1 mb-2">     .</div>
            <v-text-field
              v-validate="'max:30'"
              :error-messages="errors.collect('description')"
              data-vv-name="description"
              v-model="form.description"
              counter="30"
              maxlength="30"
              solo
              class="mb-2"
            ></v-text-field>
            <div class="headline"> </div>
            <div class="title-1 mb-2">  ,  ( 0)  .</div>
            <v-text-field
              v-validate="'required'"
              :error-messages="errors.collect('topic')"
              data-vv-name="topic"
              v-model="form.topic"
              required
              single-line
              solo
              class="mb-2"
            ></v-text-field>
            <div class="headline"> </div>
            <div class="title-1 mb-2">   .     .</div>
            <v-text-field
              v-validate="'required'"
              :error-messages="errors.collect('teamName')"
              data-vv-name="teamName"
              v-model="form.teamName"
              required
              single-line
              solo
              class="mb-2"
            ></v-text-field>
            <v-layout class="text-center">
              <v-btn color="info" tile text @click="onClear"></v-btn>
              <v-spacer></v-spacer>
              <v-btn color="success" tile text @click="onSubmit"> </v-btn>
            </v-layout>
          </v-form>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
import ko from 'vee-validate/dist/locale/ko.js';

export default {
  $_veeValidate: {
    validator: 'new'
  },
  props: {
    user: {
      type: Object
    }
  },
  data () {
    return {
      form: {
        projectId: '',
        projectName: '',
        description: '',
        topic: '',
        teamName: '',
        user: this.user
      },
      dictionary: {
        messages: ko.messages,
        attributes: {
          projectName: '  ',
          description: '  ',
          topic: '  ',
          teamName: '  '
        }
      },
      custom: {
        /*
        name: {
          required: () => "Name can not be empty",
          max: "The name field may not be greater than 10 characters"
        }
        */
      }
    }
  },
  mounted () {
    this.$validator.localize('ko', this.dictionary)
  },
  methods: {
    onSubmit () {
      this.$validator
        .validateAll()
        .then(result => {
          if (!result) throw new Error('    .')

          //  ID 
          this.form.projectId = Math.random()
            .toString(36)
            .substring(2, 12)

          //   ID  
          return this.$http.get('/projects/find', {
            params: {
              id: this.form.projectId
            }
          })
        })
        .then(result => {
          if (!result.data.id) throw new Error('  .')

          this.form.projectId = result.data.id
          return this.$http.post('/projects/create', this.form)
        })
        .then(result => {
          if (!result) throw new Error('  .')

          alert('  .')
          this.$router.push({ name: 'user' })
        })
        .catch(err => {
          alert(err.message)
        })
    },
    onClear () {
      this.form.email = '';
      this.form.password = '';
      this.form.passwordConfirm = '';
      this.form.firstName = '';
      this.form.lastName = '';
      this.$validator.reset()
    }
  }
}
</script>
